import React, { useState, useEffect } from 'react';
import './FirstPage.css';
import { Link, Redirect } from 'react-router-dom';


function FirstPage() {
  const [redirect, setRedirect] = useState(false);

  useEffect(() => {
    setTimeout(() => { setRedirect(true) }, 3000);
  });


  return (
    <div>
      {redirect ? <Redirect to="/initialmenu" /> : null }
      <div id="welcome">Bem-vindo ao Centro de Sondagens de Santo Afonso.</div>
      <p>Se não for redirecionado automaticamente,
        <Link to={"/initialmenu"} activeclassname="active"> clique aqui </Link>
      </p>
    </div>
  );
}

export default FirstPage;

