import React from 'react';
import {HashRouter, Route } from 'react-router-dom';
//import Route from './Route'; //insetad  of { Route } from 'react-router-dom';
import FirstPage from '../components/FirstPage';
import QuestionForm from '../components/QuestionForm';
import RenderServerResponse from '../components/RenderServerResponse';
import PresidentMessage from '../components/PresidentMessage';
import ContactUs from '../components/ContactUs';
import Info from '../components/Info';
import Centro from '../components/Centro';
import CurrentGeneralResults from '../components/CurrentGeneralResults';
import InitialMenu from '../components/InitialMenu';

//teste:
import Graph from '../graph/Graph';



const Routes = () => {
  return (
    <HashRouter basename='/' >
  
       <Route exact path='/' component={FirstPage} />  
        <Route path= '/initialmenu' component={InitialMenu} />
        <Route path= '/questionform' component={QuestionForm} />
        <Route path= '/renderserviceresponse' component={RenderServerResponse} />
        <Route path='/presidentmessage' component={PresidentMessage} />
        <Route path='/contactus' component={ContactUs} />
        <Route path= '/info' component={Info} />
        <Route path='/centro' component={Centro} />
        <Route path='/currentgeneralresults' component={CurrentGeneralResults} />
      
        <Route path='/graph' component={Graph} />

    </HashRouter>
  )

}

export default Routes;